import './App.css';
import React from 'react';
import { Link } from 'react-scroll';

import Main from './comp/Main';

function App() {

  const cursorRef = React.useRef(null);

  React.useEffect(()=>{
      document.addEventListener("mousemove",(event)=>{
          const { clientX, clientY } = event;
          const mouseX = clientX - cursorRef.current.clientWidth/2;
          const mouseY = clientY - cursorRef.current.clientHeight/2;

          cursorRef.current.style.transform = `translate3d(${mouseX}px,${mouseY}px,0)`;
      })
  },[]);

  return (
    <>
    <div className="menuFixed" basename="myportfolio">
        <div className="menuToggle">
          <div className="menuBurger">
            <span></span>
            <span></span>
            <span></span>
          </div>
          <ul className="navInner">
            <li><Link to= "section_fir" smooth={true} duration={1000} data-text="Home">Home</Link></li>
            <li><Link to= "section_sec" smooth={true} duration={1000} data-text="Portfolio">Portfolio</Link></li>
            <li><Link to= "section_thir" smooth={true} duration={1000} data-text="About">About</Link></li>
            <li><Link to= "section_four" smooth={true} duration={1000} data-text="Contact">Contact</Link></li>
          </ul>
        </div>
      <Main/>
    </div>
    <div className="cos-cursor" ref={cursorRef}/>
    </>
  );
}
export default App;
