import React, { useEffect } from "react";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { gsap } from "gsap";
import "./css/Contact.css"
import Resume from "./resume.pdf";
import SendMail from "./SendMail";



export default function Contact(){

  gsap.registerPlugin(ScrollTrigger);

    useEffect(() => {
      gsap.fromTo(
        ".section_four",
        {
          autoAlpha:0,
        }, {
          autoAlpha:1,
          duration: 1,
          ease: "none",
          scrollTrigger: {
            trigger: ".section_four",
            start: "top center",
            end:"bottom bottom",
            toggleActions: "play none none reverse",
        }
      });
    }, []);

    function contactShow(){
      document.querySelector('.contact-pop').classList.toggle('active');
      document.querySelector('.resume').classList.toggle('active');
    }
  return(
    <>
        <section className="section_four">
            <h2>CONTACT</h2>
            <div>
              
              <figure className="contactArea">
                  <div className="arrow">
                    <svg>
                      <path d="M238.021 79.5105L161.007 0.5H230.127L320.3 92.6177L230.127 184.5H161.009L238.021 105.726L238.851 104.877H237.663L-392.892 104.877H-394.083L-393.249 105.727L-316.012 184.5H-385.356L-475.301 92.6177L-385.356 0.5H-316.01L-393.25 79.51L-394.08 80.3595H-392.892H237.663H238.849L238.021 79.5105Z" stroke="black" fillOpacity="0"></path>
                    </svg>
                  </div>
                  <div className="contactMe">
                      <p>
                        mobile phone<br/>
                        010-8843-6480
                      </p>
                      <p onClick={()=>{window.open('https://open.kakao.com/o/sndIJhFd', '_blank')}}>
                        kakao talk<br/>
                        blingnj
                      </p>
                      <p onClick={contactShow}>
                        email<br/>
                        evelyn6480@gmail.com
                      </p>
                  </div>
              </figure>
              <div className="circle">
                <p className="resume active">
                  저에 대해 궁금하시다면
                  <a href={Resume} download>이력서</a>를 확인해주세요 :) </p>
                <SendMail/>
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
        </section>
      </>
    )
}