import './css/About.css'
import Skill from './Skill'
import imgEun from './img/eun1.jpg'
import React ,{ useEffect } from "react";
import { ScrollTrigger } from "gsap/all";
import { gsap } from "gsap"

export default function About(){

    //스크롤- opacity 변경 및 애니메이션 실행요청
    gsap.registerPlugin(ScrollTrigger);
    useEffect(()=>{
        let timeline = gsap.timeline({
            scrollTrigger:{
                trigger:".phrase",
                start:"top top",
                end:()=> document.querySelector('.phrase').offsetheight,
                pin:true,
                scrub:true,
                anticipatePin: true,
                markers: 0,
            }
        })
        timeline.to(".phrase",{ 
            autoAlpha:1,
            onStart: actionPhrase
        })
        timeline.to(".phrase",{
            autoAlpha:0,
        })
    },[])
    
    //문구 애니메이션 실행(글자분해,액션,딜레이추가)
    function actionPhrase(){
        const phrase = document.querySelector('.phrase');
        if(phrase.offsetTop <= window.scrollY){
            const text = document.querySelector('.phrase_one');
            text.innerHTML = text.textContent.replace(/\S/g, "<span class=''>$&</span>");
            const letter = document.querySelectorAll(".phrase_one span");

            for(let i=0; i<letter.length; i++){
                if( !letter[i].classList.contains('active')){
                letter[i].classList.add('active')
                letter[i].style.animationDelay = `${ i < 10? "0."+i : i/10}s`;
                }
            }
        }
    };
    return(
        <section className="section_thir">
            <figure>
                <h2>About</h2>
                <div className="profile">
                    <Skill/>
                    <div className="my-profile">
                        <div className="card" >
                            <img src={imgEun} alt="프로필사진" ></img>
                            <p className="desc" >
                                도전을 두려워 하지 않습니다
                            </p>
                            <p className="desc2">
                                <b>이름 : 조은혜</b>
                                <b>사는곳 : 서울시 관악구</b>
                                <b>E-mail : evelyn6480@gmail.com</b>
                                <b>교육사항 : UI/UX 디자인 웹퍼블리셔/프론트엔드 개발자 양성과정</b>
                                <b>교육기관 : 그린컴퓨터아카데미 강남점</b>
                            </p>
                        </div>
                    </div>
                </div>
            </figure>
            <div className="phrase" >
                <div className="phrase_one">
                    항상 더 나은 프로그램을 만들고 유지하기 위해 노력하겠습니다.
                </div>
            </div>
        </section>
    )
}