import './css/Home.css'
import React from 'react';

export default function Home(){



  function mainTextAni(e){
    const centerText = document.querySelector('.main_center')
    const txt1 = document.querySelector('.txt1')
    const txt2 = document.querySelector('.txt2')
    const txt3 = document.querySelector('.txt3')
    const txt4 = document.querySelector('.txt4')
    const txt5 = document.querySelector('.txt5')

    let moveX = -(window.innerWidth/2 - e.pageX);
    // console.log(moveX)

    centerText.style.transform = `translateX(${moveX/3}px) rotate(${moveX/30}deg)`;

    if(moveX<0){
      txt1.style.transform = `translate(60px,10px) rotate(12deg)`;
      txt2.style.transform = `translate(-180px,18px) rotate(-6deg)`;
      txt3.style.transform = `translate(0px,10px) rotate(-2deg)`;
      txt4.style.transform = `translate(80px,0px) rotate(-2deg)`;
      txt5.style.transform = `translate(-50px,0) rotate(-2deg)`;
    }else{
      txt1.style.transform = `translate(140px,10px) rotate(4deg)`;
      txt2.style.transform = `translate(-80px,20px) rotate(-8deg)`;
      txt3.style.transform = `translate(100px,10px) rotate(3deg)`;
      txt4.style.transform = `translate(200px,5px) rotate(2deg)`;
      txt5.style.transform = `translate(50px,0) rotate(3deg)`;
    }
  }

    return(
    <section className="section_fir" to="section_fir">
      <article className="main_top">
        <div>
          조은혜
        </div>
      </article>
      <article className="main_center" onMouseMove={mainTextAni}>
        <div className="txt-all txt1">
          <span>흔들려도</span>
        </div>
        <div className="txt-all txt2">
          <span>쓰러지지</span>
        </div>
        <div className="txt-all txt3">
          <span>않는</span>
        </div>
        <div className="txt-all txt4">
          <span><b>프론트앤드</b></span>
        </div>
        <div className="txt-all txt5">
          <span><b>조은혜</b>입니다</span>
        </div>
      </article>
      <article className="main_bottom_left">
        <div>
          <ul>
            <li>JavaScript / Jquery</li>
            <li>REACT / PHP </li>
            <li>plug-in 사용 및 스크립트 구현 가능</li>
          </ul>
        </div>
        <div>
          <ul>
            <li>HTML  CSS</li>
            <li>웹 표준 준수</li>
            <li>효율적인 구조로 제작</li>
            <li>크로스 브라우징 이슈 인지</li>
          </ul>
        </div>
        <div>
          <ul>
            <li>PS / AI</li>
            <li>간단한 툴 사용 및 편집 가능</li>
          </ul>
        </div>
      </article>
      <article className="main_bottom_right">
          <ul>
            <li><span>TODO LIST</span></li>
            <li><span>현대모터스튜디오</span></li>
            <li><span>한화해양생물연구센터</span></li>
          </ul>
          <p>
            <span>scroll down &darr;</span> 
          </p>
      </article>
    </section>
    )

} 