import React ,{ useEffect } from "react";
import { ScrollTrigger } from "gsap/all";
import { gsap } from "gsap";
import ImgHtml from './img/html_logo.png'
import ImgCss from './img/css_logo.png'
import ImgJavascript from './img/JavaScript_logo.png'
import ImgReact from './img/react_logo.png'
import ImgPhp from './img/php_logo.png'
import ImgPs from './img/ps_logo.jpg'
import ImgAi from './img/ai_logo.jpg'
import ImgWebpack from './img/webpack_logo.png'



export default function Skill(){

    //About 왼쪽 스킬 액션 - opacity 변경, 위치고정, Y값변경 타임라인//순차적으로 실행
    gsap.registerPlugin(ScrollTrigger);
    useEffect(()=>{
        let skillTl = gsap.timeline({
            scrollTrigger:{
                trigger:".skill_good",
                start:"top top",
                end:"bottom center",
                pin: true,
                scrub: true,
                anticipatePin: 1,
            }
        })
        let skillTl2 = gsap.timeline({
            scrollTrigger:{
                trigger:".skill_mid",
                start:"top top",
                end:"bottom center",
                pin: true,
                scrub: true,
                anticipatePin: 1,
            }
        })
        skillTl.to(".skill_good >h3 >span",{
            y:"0%",
            autoAlpha:1,
        });
        skillTl.to(".skill_good >p >span",{
            y:"0%",
            autoAlpha:1,
        });
        skillTl.to(".skill_good",{
            opacity:0,
        })
        // skillTl2.to(".skill_mid >h3 >span",{
        //     y:"0%",
        //       autoAlpha:1,
        // });
        skillTl2.to(".skill_mid >p >span",{
            y:"0%",
            autoAlpha:1,
        });
        skillTl2.to(".skill_mid",{
            opacity:0,
            duration: 0.1,
        })
    },[]);
 
    return(
        <div className="skill">
            <div className="skill_good">
                <h3><span>비중있게 다루고 있고 잘 사용합니다.</span></h3>
                <p>
                    <span><img src={ImgHtml} alt="logo"/></span>
                    <span>웹 표준 준수 및 효율적인 구조를 최우선적인 목표로 가지며 크로스 브라우징 이슈에 대해 인지하고 있습니다</span>
                </p>
                <p>
                    <span><img src={ImgCss} alt="logo"/></span>
                    <span>웹 표준 준수 하며 브라우저 크기에 대응하는 미디어쿼리 사용이 가능합니다</span>
                </p>
                <p>
                    <span><img src={ImgJavascript} alt="logo"/></span>
                    <span>plug-in 사용 및 스크립트 구현이 가능합니다</span>
                </p>
                <p>
                    <span><img src={ImgReact} alt="logo"/></span>
                    <span>plug-in 사용 및 스크립트 구현이 가능합니다</span>
                </p>
            </div>
            <div className="skill_mid">
                <h3><span>사용경험이 있으며 간단한 작업이 가능합니다</span></h3>
                <p>
                    <span><img src={ImgPhp} alt="logo"/></span>
                    <span>로그인, 게시판관리 구현 경험있습니다</span>
                </p>
                <p>
                    <span><img src={ImgWebpack} alt="logo"/></span>
                    <span>모듈화 가능합니다</span>
                </p>
                <p>
                    <span><img src={ImgPs} alt="logo"/></span>
                    <span>합성, 사진 편집 등의 2D 그래픽 작업을 비롯한 다양한 웹 시안 작업이 가능합니다</span>
                </p>
                <p>
                    <span><img src={ImgAi} alt="logo"/></span>
                    <span>간단한 툴 사용 및 편집이 가능합니다</span>
                </p>
            </div>
            {/* <div className="skill_little">
                <h3><span>저는 배움을 즐깁니다</span></h3>
                <p>
                    <span>단순한 코더가 되지 않기 위해</span>
                </p>
                <p>
                    <span>반복하고 또 반복해서 제것으로 만들겠습니다.</span>
                </p>
            </div> */}
        </div>
    )
}