import React, {useState} from 'react';

export default function SendMail(){

    // const inputBox = document.querySelectorAll('.inputBox input')
    // inputBox.forEach((v,k)=> {
    //     v.addEventListener('focus', (event) =>{
    //         event.target.parentElement.classList.add('active')
    //     });
    //     v.addEventListener('focusOut', (event) =>{
    //         if(v.value()===''){
    //             event.target.parentElement.classList.remove('active')
    //         }
    //     });
    // })
    function focusOn(event){
        event.target.parentElement.classList.add('active')
    }
    function focusOff(event){
        if(event.target.value===''){
            event.target.parentElement.classList.remove('active')
            }
    }
    const [popVisible, setPopVisible] = useState(true);
    const closeModal = () => {
        setPopVisible(false);
    }
    // function reset(){
    //     document.querySelector('.contact-form').reset()
    // }
    return(
        <div className="contact-pop">
            <form className="contact-form gform" method="POST" action="https://script.google.com/macros/s/AKfycbxIBqmzWq8W5R_tkamGCxhDgfaxgY8QF_SMwTuNgaB4zX_tFo2W0pw4dBJssdN7uGvDrA/exec">
                <div className="form-elements" >
                    <div className="inputBox">
                        <input type="text" name="name" onFocus={focusOn} onBlur={focusOff} required />
                        <label>기업명/성함/직함</label>
                    </div>
                    <div className="inputBox">
                        <input type="email" name="email"  onFocus={focusOn} onBlur={focusOff} required />
                        <label>이메일</label>
                    </div>
                    <div className="inputBox">
                        <input type="tel" id="phone" name="phone" pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"  onFocus={focusOn} onBlur={focusOff} required />
                        <label>연락처 ex) 123-456-7890</label>
                    </div>
                    <textarea placeholder="메시지를 입력해주세요" name="message" rows="5" required></textarea>
                    <button>메일 보내기</button>
                </div>
                <div className="thankyou-message" >
                    <div display={popVisible ? 'block':'none'}>
                        <em>관심 가져주셔서 감사합니다</em>
                        <em>빠른 시일 내에 연락드리겠습니다</em>
                        <button onClick={closeModal}>X</button>
                    </div>
                </div>
            </form>
        </div>
    )
}