import './css/portfolio.css'
import React,{ useEffect , useState } from 'react';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { gsap } from "gsap";
import { SliderData } from './SliderData'
import {FaArrowAltCircleRight, FaArrowAltCircleLeft } from 'react-icons/fa'

export default function Portfolio(){ 

    //스크롤- 섹션 위치 변경, 사이즈 변경
    gsap.registerPlugin(ScrollTrigger);

    useEffect(() => {

        let tl = gsap.timeline({
            scrollTrigger: {
            trigger: ".section_sec",
            start: "top 80%",
            end:"top 10%",
            scrub: true,
            }
        })
            tl.to(".sec_all", {
                duration: 1,
                scale:1,
                y:"0%",
                borderRadius:0,
                boxShadow:"none",
                width: "100%",
                height:"auto",
                filter:"none"
            })
    }, []);


    //팝업창
    const [current, setCurrent] = useState(0);
    const length = SliderData.length;

    const detail = (e)=>{
        const pop = document.querySelector('.slider')
        const body = document.querySelector('body')
            pop.classList.add('on')
            body.classList.add('off')

            let num = (e.target.dataset.value)*1

        setCurrent(num)
    }
    const popBtn = (e)=>{
        const pop = document.querySelector('.slider')
        const body = document.querySelector('body')
            pop.classList.remove('on')
            body.classList.remove('off')
    }

    const nextSlide = ()=>{
        setCurrent(current === length-1 ? 0 : current+1)
    }

    const prevSlide =()=>{
        setCurrent(current === 0 ? length-1 : current-1)
    }

    if( !Array.isArray(SliderData) || SliderData.length <= 0){
        return null;
    }

    return (
        <section className="section_sec">
            <article className="sec_all" >
                <h2>PORTFOLIO</h2>
                <figure className="project-list">
                    {
                        SliderData.map(function(list,k){
                            return  <div onClick={detail} key={k}>
                                    <img src={list.img.fir} alt={list.title}/>
                                    <b>{list.title}</b>
                                    <i>{list.hash}</i>
                                </div>
                        })
                    }
                </figure>
            </article>
            <div className="scroll-section">
                    <div class="scroll text1">
                        <div>* PRONTEND *<span> PUBLISHER </span>* PRONTEND *<span> PUBLISHER </span>* PRONTEND * <span>
                            PUBLISHER </span>* PRONTEND *<span> PUBLISHER </span>
                        </div>
                        <div>* PRONTEND *<span> PUBLISHER </span>* PRONTEND *<span> PUBLISHER </span>* PRONTEND * <span>
                            PUBLISHER </span>* PRONTEND *<span> PUBLISHER </span>
                        </div>
                    </div>
                    <div class="scroll text2">
                        <div>* HTML * CSS *<span> JAVASCROPT</span> * REACT * PHP * <span>
                            HTML</span> * CSS * JAVASCROPT * <span>REACT</span> * PHP 
                        </div>
                        <div>* HTML * CSS *<span> JAVASCROPT</span> * REACT * PHP * <span>
                            HTML</span> * CSS * JAVASCROPT * <span>REACT</span> * PHP 
                        </div>
                    </div>
                    <div class="scroll text3">
                        <div>* PROFILE * <span>JOEUNHYE</span> * PORTFOLIO * PROFILE * <span>JOEUNHYE</span> *
                            PORTFOLIO * PROFILE * <span>JOEUNHYE</span> * PORTFOLIO
                        </div>                
                        <div>* PROFILE * <span>JOEUNHYE</span> * PORTFOLIO * PROFILE * <span>JOEUNHYE</span> *
                            PORTFOLIO * PROFILE * <span>JOEUNHYE</span> * PORTFOLIO
                        </div>
                    </div>
            </div>
                <article className="slider">
                    <button style={{fontSize:'3em'}} onClick={popBtn}>X</button>
                    <FaArrowAltCircleLeft className="left-arrow" onClick={prevSlide}/>
                    <FaArrowAltCircleRight className="right-arrow" onClick={nextSlide}/>
                    {
                        SliderData.map((slide,index)=>{
                            return(
                                <div className={index === current ? "slide active" :"slide"} key={index}>
                                    {index === current && (
                                    <div className="project" >
                                        <div className="project-header">
                                            <h2>{slide.title}</h2>
                                            <div>
                                                <a href={slide.url} target="_blank" rel="noreferrer">site view</a><br/>
                                                <img src={slide.img.sec} alt="이미지" className="image"/>
                                            </div>
                                        </div>
                                        <div className="project-detail">
                                            <ul><b>제작 의도 </b>
                                                <li>
                                                    {slide.explain}
                                                </li>
                                            </ul>   
                                            <ul><b>주요 기능 </b>
                                                <li><b>{slide.func.point1}</b></li>
                                                <li>: {slide.func.detail1}</li>
                                                <li><b>{slide.func.point2}</b></li>
                                                <li>: {slide.func.detail2}</li>
                                            </ul>
                                        </div>
                                    </div>
                                    )}
                                </div>
                            )
                        })
                    }
                </article>   
        </section>
          )
}

